// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import "@/assets/js/hbbtv/hbbtv-manager-polyfill.js";
import "@/assets/js/connectivity/connector.js";

Vue.config.productionTip = false;

//helper functions
//please prefix with $
Vue.mixin({
  methods: {
    $parseParameters: function(query) {
      var dict = {};
      if (query) {
        var params = query.split("&");

        for (var i = 0; i < params.length; i++) {
          var index = params[i].indexOf("=");
          var key = index > -1 ? params[i].substr(0, index) : params[i];
          var value = index > -1 ? params[i].substr(index + 1) : "";
          if (typeof dict[key] == "undefined") {
            dict[key] = value;
          } else if (typeof dict[key] == "string") {
            dict[key] = [dict[key], value];
          } else if (typeof dict[key] == "object") {
            dict[key].push(value);
          }
        }
      }
      return dict;
    }
  }
});

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: { App },
  template: "<App/>"
});



