<template>
  <div class="start-screen">
    <div id="msg-box">
      <p>{{msg}}</p>
    </div>
    <div id="player-count-info">
      <div class="splash">
        <div id="player-nr" class="knockout-text">{{players.length}}</div>
      </div>
      <span v-if="players.length == 1">Player Connected</span>
      <span v-else>Players Connected</span>
    </div>
  </div>
</template>

<script>
import Connector from "@/components/connectors/ws-connector.js";
import Router from "@/router/index";

export default {
  name: "StartScreen",
  data() {
    return {
      query: "",
      players: Connector.players,
      msg: "Press Connect",
      minPlayers: 0,
      urls: [
        "https://media.giphy.com/media/I2sZ6qckkhfLG/giphy.gif",
        "https://media.giphy.com/media/dQRJlzW1sK3QI/giphy.gif",
        "https://media.giphy.com/media/BlcWQ9L2VfOFO/giphy.gif",
        "https://media.giphy.com/media/PzY2K7SaqIEyA/giphy.gif",
        "https://media.giphy.com/media/3oEdv5SY4LBHUBe2o8/giphy.gif",
        "https://media.giphy.com/media/tIiSuOvQucRFkoyj4W/giphy.gif",
        "https://media.giphy.com/media/mRAu3P4p28KoU/giphy.gif"
      ]
    };
  },
  created() {
    this.query = "?" + location.hash.split("?")[1];

    var vm = this;
    var csManager = oipfObjectFactory.createCSManager();
    var app2appLocalBaseUrl = csManager.getApp2AppLocalBaseURL();
    var searchParameters = vm.$parseParameters(location.hash.split("?")[1]);
    var channel = searchParameters.channel || "org.mychannel.myapp";

    setTimeout(function() {
      Connector.createConnection(0, app2appLocalBaseUrl, channel);
    }, 1000);
  },
  mounted() {
    var url =
      'url("' +
      this.urls[Math.floor(Math.random() * this.urls.length)] +
      '") repeat';
    $(".splash").css("background", url);

    var vm = this;
    document.addEventListener("initiateConfig", function() {
      vm.startGame();
    });
  },
  methods: {
    startGame() {
      if (this.players.length >= this.minPlayers) {
        this.msg = "Let's Go!";
        setTimeout(() => {
          this.players.forEach(player => {
            player.send(
              JSON.stringify({
                action: "startConfig"
              })
            );
          });

          Router.push("/game" + this.query);
        }, 3000);
      } else {
        this.msg = "You need at least 4 players!";
        setTimeout(() => {
          this.msg = "drawnout.tv";
        }, 3000);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.start-screen {
  position: relative;
}
#msg-box {
  position: absolute;
  top: 10vh;
  right: 30%;
  font-size: 1.5em;
  z-index: 5;
}

#player-nr {
  font-size: 5em;
  position: relative;
  margin: 0 0 5vh 0;
}

#player-count-info {
  font-size: 3em;
  text-align: center;
  padding-top: 20vh;
  position: relative;
}
</style>



