<template>
  <div class="intermediate-screen">
    <h1 id="msg">{{msg.toUpperCase()}}</h1>
  </div>
</template>


<script>
export default {
  name: "intermediate-screen",
  props: ["msg"]
};
</script>


<style scoped>
#msg {
  /* height: 90%; */
  word-wrap: break-word;
  width: 50%;
  text-align: center;
  background: white;
  margin: 0 auto;
  padding: 10px;
}
.intermediate-screen {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(110deg, #fdcd3b 60%, #ffed4b 60%);
}
</style>



