//list of players/connections

import DB from "../DrawingBoard.vue";
import Game from "../Game.vue";
var players = [];

function createConnection(index, app2appLocalBaseUrl, channel) {
  console.log(app2appLocalBaseUrl);
  console.log(channel);
  var ws = new WebSocket(app2appLocalBaseUrl + channel);
  ws.binaryType = "arraybuffer";
  ws.onopen = function(evt) {
    console.log("Connection " + index + " waiting ...");
  };
  ws.onclose = function(evt) {
    console.log("Connection " + index + " closed.");
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] === ws) {
        players.splice(i, 1);
      }
    }

    //TODO: player disconnect message
  };
  ws.onerror = function(evt) {
    console.log("Connection error.");
  };
  ws.onmessage = function(evt) {
    if (evt.data == "pairingcompleted") {
      console.log("connection " + index + " paired");
      players.push(ws);
      players[0].send(
        JSON.stringify({
          action: "setMaster"
        })
      );
      ws.send(
        JSON.stringify({
          action: "setConnected"
        })
      );
      ws.onmessage = function(evt) {
        var json = JSON.parse(evt.data);
        console.log(json);
        switch (json.action) {
          case "initiateConfig":
            console.log("here");

            var initiateConfig = new CustomEvent("initiateConfig");
            document.dispatchEvent(initiateConfig);
            break;
          case "setConfig":
            var setConfig = new CustomEvent("setConfig", {
              detail: json.payload
            });
            document.dispatchEvent(setConfig);

          // break;

          case "startRound":
            var startRound = new CustomEvent("startRound");
            document.dispatchEvent(startRound);
            break;

          case "draw":
            console.log("in switch");
            DB.methods.draw(json.payload);
            break;

          case "moveContext":
            DB.methods.moveContext(json.payload);
            break;
          case "endPath":
            DB.methods.endPath(json.payload);
            break;
        }
      };
      createConnection(index + 1, app2appLocalBaseUrl, channel);
    } else {
      console.log("Unexpected message received from terminal.");
      ws.close();
    }
  };
}

export default {
  createConnection,
  players
};



