<template>
  <div class="drawing-board">
    <div id="current-round-info">
      <span class="info-display">Round {{currentGameValues.currentRound}} / {{config.maxRounds}}</span>
      <span class="info-display" id="current-word-display">{{currentGameValues.maskedWord}}</span>
      <span class="info-display">{{currentGameValues.timer}}</span>
    </div>
    <div id="canvas-container">
      <canvas :width="width" :height="height" id="canvas"></canvas>
    </div>
  </div>
</template>


<script>
export default {
  name: "drawing-board",
  props: ["currentGameValues", "config"],
  data: function() {
    return {
      width: window.innerWidth * 0.9,
      height: window.innerHeight * 0.9
    };
  },
  computed: {},
  methods: {
    // draw: function(event) {
    //   // requestAnimationFrame(this.draw);
    //   if (this.mouse.down) {
    //     var c = document.getElementById("canvas");

    //     var ctx = c.getContext("2d");

    //     ctx.clearRect(0, 0, 800, 800);

    //     ctx.lineTo(this.currentMouse.x, this.currentMouse.y);
    //     ctx.strokeStyle = "#F63E02";
    //     ctx.lineWidth = 2;
    //     ctx.stroke();
    //   }
    // },
    draw: function(msg) {
      // requestAnimationFrame(this.draw);
      var c = document.getElementById("canvas");

      var ctx = c.getContext("2d");

      // ctx.clearRect(0, 0, 800, 800);
      console.log("in draw");
      console.log(msg);

      const scaleFactorWidth = window.innerWidth / msg.drawingViewport.width;
      const scaleFactorHeight = window.innerHeight / msg.drawingViewport.height;

      ctx.lineTo(
        msg.coordinates.x * scaleFactorWidth,
        msg.coordinates.y * scaleFactorHeight
      );

      ctx.strokeStyle = msg.strokeStyle;
      ctx.lineWidth = msg.lineWidth;
      ctx.stroke();
    },
    moveContext(msg) {
      console.log("in move context");
      var c = document.getElementById("canvas");
      var ctx = c.getContext("2d");

      const scaleFactorWidth = window.innerWidth / msg.drawingViewport.width;
      const scaleFactorHeight = window.innerHeight / msg.drawingViewport.height;
      ctx.beginPath();
      ctx.moveTo(
        msg.coordinates.x * scaleFactorWidth,
        msg.coordinates.y * scaleFactorHeight
      );
    },
    endPath(msg) {
      var c = document.getElementById("canvas");
      var ctx = c.getContext("2d");

      ctx.closePath();
    }
  },
  ready: function() {
    var c = document.getElementById("canvas");
    var ctx = c.getContext("2d");
    ctx.translate(0.5, 0.5);
    ctx.imageSmoothingEnabled = false;
    // this.draw();
  }
};
</script>

<style scoped>
#current-round-info {
  margin: 1em 0 0.5em 0;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
#canvas-container {
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
canvas {
  background-color: aliceblue;
}
#current-word-display {
  letter-spacing: 10px;
}
.info-display {
  /* color: white; */
  /* background-color: black; */
  /* padding: 4px; */
  font-style: italic;
}
</style>




