<template>
  <div class="game">
    <IntermediateScreen id="preparing-screen" :msg="`Create a new game!`" v-if="gamePhase == -1"></IntermediateScreen>

    <IntermediateScreen
      id="preparing-screen"
      :msg="`prepare for round ${currentGameValues.currentRound}`"
      v-if="gamePhase == 0"
    ></IntermediateScreen>

    <div v-else-if="gamePhase == 1" id="game-screen">
      <div id="drawing-board">
        <DrawingBoard :currentGameValues="currentGameValues" :config="config"></DrawingBoard>
      </div>
    </div>

    <IntermediateScreen
      id="round-end-screen"
      :msg="`Round Over \n New Teams`"
      v-else-if="gamePhase == 2"
    ></IntermediateScreen>

    <IntermediateScreen id="round-end-screen" :msg="`Game Over`" v-else-if="gamePhase == 3"></IntermediateScreen>
  </div>
</template>

<script>
// import * as GameManager from "@/components/game/GameManager.js";
import DrawingBoard from "@/components/DrawingBoard";
import IntermediateScreen from "@/components/intermediateScreen";
import Connector from "@/components/connectors/ws-connector.js";

export default {
  name: "game",
  components: { DrawingBoard, IntermediateScreen },
  data() {
    return {
      gamePhase: -1, // -1 wait for config,  0 preparing game, 1 round in progess, 2 round over, 3 game over
      players: Connector.players, // Array of Websocket connections representing players
      wordlist: ["testword", "supertest", "superultratest"], // list of words that can be chosen by the game
      config: {
        guessTeamSize: 2, // number of players in a guessing team
        drawTeamSize: 2, // number of players in a drawing team
        roundTimeSec: 30, // Max time per Round in Seconds
        maxRounds: 10 // amount of Rounds in a game
      },
      currentGameValues: {
        currentWord: "", // word the drawing team has to draw
        maskedWord: "", // masked currentword
        currentRound: 0, // current round of the game
        guessTeams: [], // Array of teams. Each team array of WS-Connections
        drawTeam: [], // Single team (array of ws-connections) that draws word in current round
        timer: 0
      }
    };
  },

  mounted() {
    console.log("players at game mounted : " + this.players.length);
    // this.startRound();
    var vm = this;
    document.addEventListener("setConfig", function(e) {
      vm.setGameConfig(e.detail);
    });
    document.addEventListener("startRound", function(e) {
      vm.startRound();
    });
  },
  methods: {
    startRound() {
      //starts a new Round
      var vm = this;
      this.currentGameValues.currentRound++;
      this.gamePhase = 0;
      this.buildTeams();
      console.log("after build teams", this.players.length);

      this.selectWord();
      this.maskWord();
      this.setTimer();
      setTimeout(() => {
        this.gamePhase = 1;
        for (var i = 0; i < this.players.length; i++) {
          this.players[i].send(
            JSON.stringify({
              action: "setGamePhase",
              payload: 1
            })
          );
        }

        this.startTimer();
      }, 3000);
      //TODO: enableGuessing
      //TODO: enableDrawing
    },

    startTimer() {
      // starts the timer and handles stuff during the round
      this.gamephase = 1;
      var interval = setInterval(() => {
        if (this.currentGameValues.timer > 0) {
          this.currentGameValues.timer--;
          // this.unMaskWord();
        } else {
          clearInterval(interval);
          this.endRound();
        }
      }, 1000);
    },
    endRound() {
      //ends Round
      this.gamePhase == 2;
      for (var i = 0; i < this.players.length; i++) {
        this.players[i].send(
          JSON.stringify({
            action: "setGamePhase",
            payload: 2
          })
        );
      }
      if (this.currentGameValues.currentRound == this.config.maxRounds) {
        this.gameover();
      } else {
        //TODO: evaluate points
      }
      //TODO: manage points
      setTimeout(() => {
        this.startRound();
      }, 2000);
    },

    gameOver() {
      this.gamestate = 3;
      for (var i = 0; i < this.players.length; i++) {
        this.players[i].send(
          JSON.stringify({
            action: "setGamePhase",
            payload: 3
          })
        );
      }
    },
    setTimer() {
      this.currentGameValues.timer = this.config.roundTimeSec;
    },
    maskWord() {
      //TODO: rework this
      var maskedWord = [];
      for (var i = 0; i < this.currentGameValues.currentWord.length; i++) {
        maskedWord.push("_");
      }
      this.currentGameValues.maskedWord = maskedWord.join("");
    },
    unMaskWord() {
      var timerPercentage =
        this.currentGameValues.timer / this.config.roundTimeSec;
      var ranNo = Math.random();
      var maskedWord = [];
      for (var i = 0; i < this.currentGameValues.currentWord.length; i++) {
        if (ranNo > timerPercentage) {
          if (this.currentGameValues.maskedWord.charAt(i) == "_") {
            maskedWord.push(this.currentGameValues.currentWord.charAt(i));
          } else {
            maskedWord.push("_");
          }
        } else {
          if (this.currentGameValues.maskedWord.charAt(i) == "_") {
            maskedWord.push(this.currentGameValues.maskedWord.charAt(i));
          }
        }
      }
      this.currentGameValues.maskedWord = maskedWord.join("");
    },

    setGameConfig(gameConfig) {
      this.config.guessTeamSize = gameConfig.guessTeamSize;
      this.config.drawTeamSize = gameConfig.drawTeamSize;
      this.config.roundTimeSec = gameConfig.roundTimeSec;
      this.config.maxRounds = gameConfig.maxRounds;
      // send start game to all phones

      this.players.forEach(player => {
        player.send(
          JSON.stringify({
            action: "startRound"
          })
        );
      });

      console.log("game config 3 " + this.players.length);
    },

    shuffle(o) {
      for (
        var j, x, i = o.length;
        i;
        j = Math.floor(Math.random() * i), x = o[--i], o[i] = o[j], o[j] = x
      );
      return o;
    },

    selectWord() {
      var ranNo = Math.floor(Math.random() * this.wordlist.length);
      this.currentGameValues.currentWord = this.wordlist[ranNo];
    },

    buildTeams() {
      var clone = [...this.players];
      const shuffledPlayers = this.shuffle(clone); // maybe not needed?

      //fill drawTeam
      for (var i = 0; i <= this.config.drawTeamSize; i++) {
        if (shuffledPlayers.length > 0) {
          const ranNo = Math.floor(Math.random() * shuffledPlayers.length);

          const ranPlayer = shuffledPlayers[ranNo];

          this.currentGameValues.drawTeam.push(ranPlayer);
          shuffledPlayers.splice(ranNo, 1);
          console.log("ranplayer", ranPlayer);

          ranPlayer.send(
            JSON.stringify({
              action: "assignTeam",
              payload: "draw"
            })
          );
        } else {
          console.log("draw teams not enough players");

          break;
        }
      }
      //fill guessTeam

      var noOfTeams = Math.floor(
        this.players.length / this.config.guessTeamSize
      );
      for (var x = 0; x <= noOfTeams; x++) {
        var newTeam = [];

        for (var i = 0; i <= this.config.guessTeamSize; i++) {
          if (shuffledPlayers.length > 0) {
            const ranNo = Math.floor(Math.random() * shuffledPlayers.length);

            const ranPlayer = shuffledPlayers[ranNo];
            newTeam.push(ranPlayer);
            shuffledPlayers.splice(ranNo, 1);

            ranPlayer.send(
              JSON.stringify({
                action: "assignTeam",
                payload: "guess_" + x
              })
            );
          } else {
            console.log("guess teams not enough players");

            break;
          }
        }
        this.currentGameValues.guessTeams.push(newTeam);
      }
    },

    evaluateGuess(guess, player) {
      // returns True or False
      if (guess == this.currentGameValues.currentWord) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
</style>



