<template>
  <div class="splash bigger">
    <h2 class="knockout-text">DRAWNOUT</h2>
  </div>
</template>
<script>
import { setTimeout } from "timers";
import Router from "@/router/index";

export default {
  name: "splash",
  data() {
    return {
      query: "",
      urls: [
        "https://media.giphy.com/media/I2sZ6qckkhfLG/giphy.gif",
        "https://media.giphy.com/media/dQRJlzW1sK3QI/giphy.gif",
        "https://media.giphy.com/media/BlcWQ9L2VfOFO/giphy.gif",
        "https://media.giphy.com/media/PzY2K7SaqIEyA/giphy.gif",
        "https://media.giphy.com/media/3oEdv5SY4LBHUBe2o8/giphy.gif",
        "https://media.giphy.com/media/tIiSuOvQucRFkoyj4W/giphy.gif",
        "https://media.giphy.com/media/mRAu3P4p28KoU/giphy.gif"
      ]
    };
  },
  created() {
    var vm = this;
    var searchParameters = vm.$parseParameters(location.hash.split("?")[1]);
    vm.query = "?" + location.hash.split("?")[1];
  },
  mounted() {
    var url =
      'url("' +
      this.urls[Math.floor(Math.random() * this.urls.length)] +
      '") repeat';
    $(".splash").css("background", url);
    setTimeout(_ => {
      $(".splash").addClass("fadeOut");
      setTimeout(_ => {
        console.log(this.query);
        Router.push("/start" + this.query);
      }, 300);
    }, 4000);
  }
};
</script>

<style scoped>
.bigger {
  animation: scaleUpToNormal 100ms linear;
}
.huge {
  animation: scaleUpToHuge 300ms linear;
}

.fadeOut {
  animation: fadeOut 300ms linear;
}

h2 {
  font-size: 6em;
  padding-top: 25vh;
}
</style>



